.glitch {
	position: relative;
}

.glitch:after {
	content: attr(data-text);
	position: absolute;
	left: 3px;
	top: 0;
	overflow: hidden;
	text-shadow: -3px 0 #000;
	clip: rect(0, 700px, 0, 0);
	animation: noise-anim 3s infinite ease alternate-reverse;
}
.glitch:before {
	content: attr(data-text);
	position: absolute;
	right: -3px;
	top: 0;
	overflow: hidden;
	clip: rect(0, 700px, 0, 0);
	text-shadow: 3px 0 #000;
	animation: noise-anim-2 4s infinite ease alternate-reverse;
}


@keyframes noise-anim {
  0% {
    clip: rect(126px, 700px, 32px, 30px);
  }
  10% {
    clip: rect(15px, 700px, 102px, 30px);
  }
  20% {
    clip: rect(44px, 700px, 31px, 30px);
  }
  30% {
    clip: rect(36px, 700px, 6px, 30px);
  }
  40% {
    clip: rect(119px, 700px, 30px, 30px);
  }
  50% {
    clip: rect(152px, 700px, 150px, 30px);
  }
  60% {
    clip: rect(136px, 700px, 74px, 30px);
  }
  70% {
    clip: rect(138px, 700px, 61px, 30px);
  }
  80% {
    clip: rect(99px, 700px, 10px, 30px);
  }
  90% {
    clip: rect(54px, 700px, 133px, 30px);
  }
  100% {
    clip: rect(114px, 700px, 6px, 30px);
  }
}
